@import "variables";

body{
  .asideButtons{
    display: none;
    position: fixed;
    z-index: 10;
    right: 0;
    bottom: 40%;
    ul{
      li{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        margin: 10px;
        background-color: #e4f4f5;
        border-radius: 5px;
        span{
          width: 60px;
          height: 60px;
          display: flex;
          justify-content: center;
          align-items: center;
          text-decoration: none;
          i{
            color: @mainColor;
          }
          &:hover{
            cursor: pointer;
          }
        }
        .rollUp{
          font-size: 1.5em;
        }
        .phone{
          font-size: 2em;
        }
      }
    }
  }
  .feedbackFormPopup{
    width: 375px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    border-radius: 5px;
    h5{
      margin: 25px 0;
      font: 1.25em/1.375em @sansbold;
      color: @mainColor;
    }
    .userData{
      margin: 5px 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      input{
        width: 215px;
        height: 40px;
        margin: 5px 0;
        padding: 0 15px;
        font: 1em/40px @sansregular;
        color: #828282;
        border: 1px solid #e6e6e6;
        border-radius: 5px;
        outline: none;
      }
      textarea{
        width: 215px;
        height: 90px;
        resize: none;
        margin: 5px 0;
        padding: 0 15px;
        font: 1em/40px @sansregular;
        color: #828282;
        border: 1px solid #e6e6e6;
        border-radius: 5px;
        outline: none;
      }
      span{
        margin: 15px 0 5px;
        display: block;
        font: 1em/1.25em @sansregular;
        color: #474747;
      }
      p{
        display: flex;
        font: 1em/40px @sansregular;
        color: #474747;
        input{
          margin: 0 10px;
          width: 65px;
          height: 40px;
          font: 1em/40px @sansregular;
          color: #828282;
          border: 1px solid #e6e6e6;
          border-radius: 5px;
          outline: none;
        }
      }
      .error{
        background-color: @errorColor;
      }
    }
    button{
      margin: 40px 0;
      width: 195px;
      height: 40px;
      font: 1em/40px @sansregular;
      color: #fff;
      text-transform: uppercase;
      background-color: @mainColor;
      border: none;
      border-radius: 5px;
      outline: none;
      &:hover{
        cursor: pointer;
      }
    }
  }
  #feedbackConfirm{
    margin: 0 auto;
    padding: 15px 0;
    width: 380px;
    min-height: 100px;
    position: relative;
    text-align: center;
    background: #fff;
    border-radius: 5px;
    p{
      margin: 25px 0;
      font: 1.125em/1.25em @sansbold;
      color: @mainColor;
    }
    span{
      display: block;
      font: 1em/1.125em @sansregular;
      color: #474747;
    }
    button{
      margin: 40px 0;
      width: 195px;
      height: 40px;
      font: .875em/40px @sansregular;
      color: #fff;
      text-align: center;
      text-transform: uppercase;
      background-color: @mainColor;
      border: none;
      border-radius: 5px;
      outline: none;
      &:hover{
        cursor: pointer;
      }
    }
  }
}